body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
header{
  background-color: black;
  color:white;
  padding:20px 30px;
  display:flex;
  justify-content: space-between;
  font-size:30px;
  font-family:Arial, Helvetica, sans-serif;
  letter-spacing:2px ;
  align-items: center;
  justify-content: center;
}
.flex-center{
  display: flex;
  align-items: center;
  justify-content: center;

}
.QuizStart{
  text-align: center;
  padding-top: 200px;
}
.container{
  
  height: 80vh;
}
.quiz-start {
  flex-direction: column;
  gap: 20px;
}
.quiz-start button{
  background-color: black;
  color: white;
  padding: 5px 9px;
  border-radius: 3px;
}
.wrapper
{
  border: 1px solid grey;
  border-radius: 3px;
  padding:10px;
  min-width:200px;
  display: flex;
  flex-direction: column;
  gap:20px;
}
.options
{
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.question{
  font-weight: bold;
}
.option input {
  margin-right: 5px;
}
.wrapper button{
  background-color: black;
  color: white;
  padding: 7px;
  border-radius: 3px;
}